import * as api from '../api/'


export const getSurveyStatus = (token) => {
	return dispatch => {
		dispatch({ type: 'admin/wait'});
		api.getSurveyStatus(token)
		.then(resp => {
			dispatch({
				type: 'admin/surveys',
				payload: resp.data
			});
		})
		.catch(e => console.log(e));
	}
}

export const postSurveyStartByAdmin = (token, surveyId) => {
	return dispatch => {
		dispatch({ type: 'admin/wait', payload: null });
		api.postSurveyStart(token, surveyId)
		.then(resp => dispatch(getSurveyStatus(token)))
		.catch(e => console.log(e));
	}
}



export const postParticipantEmailSent = (token, surveyId, personId) => {
	return dispatch => {
		dispatch({ type: 'admin/participant', payload: { wait: true }});
		api.postParticipantEmailSent(token, surveyId, personId)
		.then(resp => {
			dispatch({
				type: 'admin/participant',
				payload: resp.data
			});
		})
		.catch(e => console.log(e));
	}
} 

