import React, { useRef, useEffect, useState } from "react"
import { Container, Row, Col, Navbar, Nav, Table } from "react-bootstrap"

import { Menu } from "../menu"
import { LoginModal } from '../loginview'
import { DrawXAxis, DrawYAxis, DrawData, RemoveAxis, DrawBoxData } from "./drawaxis"

export default function ReportView() {

  const svgRef = useRef(null);
  const [state, setState] = useState(false);

	useEffect(() => {
		DrawXAxis(svgRef,50,150);
		DrawYAxis(svgRef,50,150);		
		DrawData(svgRef,50,150);		
	}, []);
	
	function switchView() {
		RemoveAxis(svgRef);
		setState(!state);
		if(state) {
			DrawXAxis(svgRef,50,150);
			DrawYAxis(svgRef,50,150);		
			DrawData(svgRef,50,150);		
		} else {
			DrawBoxData(svgRef);
		}
	}


	return (
	<>
		<Menu />
		<LoginModal />
		<Container>
			<Row>
			<Col sm={5} md={6} lg={7} >
			<h4>HRC Indeksin tulokset</h4>
			<Table>
			<tbody>
			<tr><td>Personnel planning</td><td>85</td></tr>
			<tr><td>Competence management</td><td>68</td></tr>
			<tr><td>Performance management</td><td>75</td></tr>
			<tr><td>Staff wellbeing</td><td>82</td></tr>
			<tr><td>Contract management</td><td>73</td></tr>
			<tr><td>Legal obligations</td><td>90</td></tr>
			<tr><td>People management targets</td><td>89</td></tr>
			<tr><td style={{'textAlign':'right'}}><b>total</b></td><td><b>78</b></td></tr>
			</tbody>
			</Table>
			</Col>
			<Col sm={7} md={6} lg={5}>
			<svg onClick={switchView} className="d-block mx-auto" viewBox="-175,-175,350,350" strokeLinejoin="round" strokeLinecap="round" width={350} height={350} ref={svgRef} style={{ background: "white", margin: "10px" }} >
				<g className="xaxis"/>
				<g className="yaxis"/>
				<g className="data" />
      		</svg>
      		</Col>
      		</Row>
		</Container>
	</>
	);
}