import { ApiClient } from './client'

export function postEmailAttachment(token, surveyId, contenttype, content) {
	console.log('API.postEmailAttachment');
	console.log(surveyId);
	console.log(contenttype);
	console.log(content);
	const client = ApiClient(token, { "Content-Type": contenttype })
	return client.post('/survey/' + surveyId + '/emails', content);
}

export function getSurveyList(token) {
	const client = ApiClient(token);
	return client.get('/company');
}

export function postSurveyStart(token, surveyId) {
	const client = ApiClient(token);
	return client.get('/survey/' + surveyId + '/start')
}