import React, { useState } from "react"
import { Button, Form } from "react-bootstrap"
import { useSelector, useDispatch } from 'react-redux';
import { postEmailAttachment } from '../actions'

export function FileDrop({surveyId}) {
	
	const dispatch = useDispatch();
	const token = useSelector((state) => state.authorization.token)

  const onDragOver = (event) => {
    event.stopPropagation();
    event.preventDefault();
  }

  const onDragEnter = (event) => {
    event.stopPropagation();
    event.preventDefault();
  }

  const onFileDrop = (event) => {
    event.stopPropagation();
    event.preventDefault();

    const files = event.dataTransfer.files;
    const metadata = [];
    for (var i = 0; i < files.length; i++) {
      const file = files[i];
      metadata.push({
        name: file.name,
        type: file.type,
        size: file.size,
        modified: file.lastModified,
        file: file
      })
	  dispatch(postEmailAttachment(token, surveyId,file.type, file));
    }
  }

  const onChange = (event) => {
    event.stopPropagation();
    event.preventDefault();

    const files = event.target.files;
    const metadata = [];
    for (var i = 0; i < files.length; i++) {
      const file = files[i];
      metadata.push({
        name: file.name,
        type: file.type,
        size: file.size,
        modified: file.lastModified,
        file: file
      })
	  dispatch(postEmailAttachment(token, surveyId,file.type, file));
    }
    
    console.log(metadata);
  }


	return (
		<Form.Group onDragEnter={onDragEnter} onDragOver={onDragOver} onDrop={onFileDrop}>
        <Form.Control id="selectedFile" size="sm" style={{'display': 'none'}} onChange={onChange} type="file" />
		<Button className="w-100" variant="outline-secondary" size="sm"  onClick={() => document.getElementById('selectedFile').click()} >Drop files here</Button>
      	</Form.Group>
	);
}