import React, { useState, useEffect} from "react"
import { Container, Form, Row, Col, Accordion, Badge, Table, Button, Spinner } from "react-bootstrap"
import { Menu } from "../menu"
import { LoginModal } from '../loginview'
import { ParticipantModal } from './participantmodal'
import { ConfirmationModal } from '../common/confirmationmodal'
import { useSelector, useDispatch } from 'react-redux'
import { getSurveyStatus, postParticipantEmailSent, postSurveyStartByAdmin } from '../actions'

import { DraftSurvey } from '../common/draftsurvey'
import { StartedSurvey } from '../common/startedsurvey'
import { FinishedSurvey } from '../common/finishedsurvey'


export default function AdminDashboardView() {
	
	const dispatch = useDispatch()
	const token = useSelector((state) => state.authorization.token)
	const participant = useSelector((state) => state.admin.participant)
	const surveys = useSelector((state) => state.admin.surveys)
	
	const [surveyWaitingConfirmation, setSurveyWaitingConfirmation ] = useState(null);
	
	useEffect(() => {
		if(token !== null && participant === null) {
			dispatch(getSurveyStatus(token))
		}
	}, [token, participant])
	
	const postParticipantEmail = (surveyId) => {
		dispatch(postParticipantEmailSent(token, surveyId, null))
	}
	
	const onStart = (surveyId) => {
		setSurveyWaitingConfirmation(surveyId);
	}
	
	const onStartConfirmed = () => {
		dispatch(postSurveyStartByAdmin(token, surveyWaitingConfirmation))
		setSurveyWaitingConfirmation(null);
	}
		
	const surveylist = surveys === null ? [] : surveys
	
	return (
	<>
		<Menu />
		<LoginModal />
		<ParticipantModal />
		<ConfirmationModal show={surveyWaitingConfirmation != null} title="Start Survey" text="Confirm starting the survey" onCancel={() => setSurveyWaitingConfirmation(null)} onOk={onStartConfirmed} />
		<Container>
			<Row>
			<h4>Survey Status</h4>
			</Row>
			{ surveys === null && <Spinner animation="grow" />}
			<Accordion defaultActiveKey={0} flush >
			{surveylist.map(survey => {
				const variant = survey.status === 'DRAFT' ? 'danger' : (survey.status === 'STARTED' ? 'warning' : 'success')
				return (
					<Accordion.Item key={survey.surveyId} eventKey={survey.surveyId}>
					<Accordion.Header>{survey.company} - {survey.name}<Badge pill bg={variant} className="ms-auto me-1">{survey.status}</Badge></Accordion.Header>
					<Accordion.Body>
					{ survey.status === 'DRAFT' && <DraftSurvey onStart={onStart} survey={survey} /> }
					{ survey.status === 'STARTED' && <StartedSurvey onClickSend={postParticipantEmail} survey={survey} /> }
					{ survey.status === 'FINISHED' && <FinishedSurvey survey={survey} /> }
					</Accordion.Body>
					</Accordion.Item>
				)
			})}
			</Accordion>
		</Container>
	</>
	)
}