import * as api from '../api/'

export const postEmailAttachment = (token, surveyId, contenttype, file) => {
	console.log('ACTIONS.postEmailAttachment');
	console.log(surveyId);
	console.log(contenttype);
	console.log(file);

	return dispatch => {
		const reader = new FileReader();
		reader.onload = event => {
			api.postEmailAttachment(token, surveyId, contenttype, event.target.result)
			.then((resp) => {
				dispatch({
					type: 'survey/list',
					payload: resp.data
				});
			});
		}
		reader.readAsArrayBuffer(file);
	}
}

export const getSurveyList = (token) => {
	return dispatch => {
		api.getSurveyList(token)
		.then(resp => {
			dispatch({
				type: 'survey/list',
				payload: resp.data
			});
		})
		.catch(e => console.log(e));
	}
}

export const postSurveyStartByCompany = (token, surveyId) => {
	return dispatch => {
		dispatch({ type: 'survey/wait', payload: null });
		api.postSurveyStart(token, surveyId)
		.then(resp => dispatch(getSurveyList(token)))
		.catch(e => console.log(e));
	}
}
