import { ApiClient } from './client'

export function getSurveyStatus(token) {
	const client = ApiClient(token);
	return client.get('/rest/admin/status');
}

export function postParticipantEmailSent(token, surveyId, personId) {
	const client = ApiClient(token);
	const params = {
		surveyId: surveyId,
		personId: personId
	}
	return client.post('/rest/admin/survey/url', params)
}


export function getUsers(token) {
	const client = ApiClient(token);
	return client.get('/rest/admin/users');
}


export function postPasswordReset(token, adminId) {
	const client = ApiClient(token);
	const params = {
		personId: adminId
	}
	return client.post('/rest/admin/passwordreset', params)
}