import React from 'react'
import { Accordion, Badge, Table, Row, Col, Button } from 'react-bootstrap'
import { useSelector, useDispatch } from 'react-redux'
import { DraftSurvey } from '../common/draftsurvey'
import { StartedSurvey } from '../common/startedsurvey'
import { FinishedSurvey } from '../common/finishedsurvey'


export function Survey({survey, onStart}) {

	const dispatch = useDispatch()
	const token = useSelector((state) => state.authorization.token)

	const variant = survey.status === 'DRAFT' ? 'danger' : (survey.status === 'STARTED' ? 'warning' : 'success')
	const editable = survey.status === 'DRAFT'
	
	const participants = survey.status === 'STARTED' ? (survey.participantsFinished + '/' + survey.participantsAll) : survey.participantsAll

	return (
		<Accordion.Item eventKey={survey.surveyId}>
			<Accordion.Header>{survey.name}<Badge pill bg={variant} className="ms-auto me-1">{survey.status}</Badge></Accordion.Header>
			<Accordion.Body>
			{ survey.status === 'DRAFT' && <DraftSurvey onStart={onStart} survey={survey} /> }
			{ survey.status === 'STARTED' && <StartedSurvey survey={survey} /> }
			{ survey.status === 'FINISHED' && <FinishedSurvey survey={survey} /> }
			</Accordion.Body>
		</Accordion.Item>
	);
}