import React, { useState, useEffect} from "react"
import { Container, Row, Accordion,Spinner } from "react-bootstrap"
import { Menu } from "../menu"
import { LoginModal } from '../loginview'
import { ConfirmationModal } from '../common/confirmationmodal'
import { useSelector, useDispatch } from 'react-redux'
import { getSurveyList, postSurveyStartByCompany } from '../actions'

import { Survey } from './survey'

export default function SurveyView() {
	
	const dispatch = useDispatch()
	const company = useSelector((state) => state.survey)
	const token = useSelector((state) => state.authorization.token)
	
	const [surveyWaitingConfirmation, setSurveyWaitingConfirmation ] = useState(null);

	useEffect(() => {
		dispatch(getSurveyList(token));
	}, [])
	
	const onStart = (surveyId) => {
		setSurveyWaitingConfirmation(surveyId);
	}
	
	const onStartConfirmed = () => {
		dispatch(postSurveyStartByCompany(token, surveyWaitingConfirmation))
		setSurveyWaitingConfirmation(null);
	}
	
	const surveys = company.surveys === null ? [] : company.surveys;
	const defaultKey = surveys.length == 0 ? 0 : surveys[0].surveyId;
	
	return (
	<>
		<Menu />
		<LoginModal />
		<ConfirmationModal show={surveyWaitingConfirmation != null} title="Start Survey" text="Confirm starting the survey" onCancel={() => setSurveyWaitingConfirmation(null)} onOk={onStartConfirmed} />
		<Container>
			<Row>
			<h4>Kyselytilanne</h4>
			</Row>
			{ company.surveys === null && <Spinner animation="grow" />}
			<Accordion defaultActiveKey={defaultKey} >
			{surveys.map(survey => {
				return <Survey key={survey.surveyId} survey={survey} onStart={onStart} />
			})}
			</Accordion>
		</Container>
	</>
	)
}